/* Prevent selection while dragging */
.dragging, .dragging * {
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}
.dragging::-moz-selection,  .dragging *::-moz-selection {
	background-color: transparent;
}
.dragging::selection,  .dragging *::selection {
	background-color: transparent;
}
